.App {
  width: 100%;
  height: 100%;
  display: flex;

  .pointer {
    cursor: pointer;
  }

  .ant-layout.ant-layout-has-sider{
    width: 100%;
  }

  .icon-btn {
    color: #ffd152;
    cursor: pointer;
  }

  .icon-btn:hover {
    color: #ebac00;
  }

  .side-bar {
    background-color: #574b4f;
    position: relative;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;

    .ant-layout-sider-children {
      overflow-y: auto;
    }

    .ant-layout-sider-trigger{
      background-color: #574b4f;
    }

    .ant-menu.ant-menu-root{
      width: 100%;
      background-color: transparent;
      border-right: 1px solid transparent;
      padding-top: 20px;
      li {
        background-color: transparent;
        color: #ffd152;
        svg{
          height: 2rem;
          width: 2rem;
        }
        margin-bottom: 14px;
      }
      li:hover{
        color: #ebac00;
      }
      .ant-menu-selected::after,.ant-menu-item-selected::after{
        // display: none;
        border-right: 8px solid #ffd152;
      }
      .ant-menu-submenu-title:hover{
        color: #ebac00;
      }
      .ant-menu-sub{
        background-color: transparent;
      }
      .ant-menu-submenu-arrow:before{
        background: #ffd152;
      }
      .ant-menu-submenu-arrow:after{
        background: #ffd152;
      }
    }
  }

  .side-bar.ant-layout-sider-collapsed{
    .forward-arrow.icon-btn {
      transform: rotate(180deg);
    }
  }

  ::-webkit-scrollbar {
    width: 6px;
    background-color: #574b4f;
  }
  ::-webkit-scrollbar-track {
      background: #574b4f;
      width: 6px;
  }
  ::-webkit-scrollbar-thumb {
      background-color: #ffd152;
      border-radius: 6px;
  }
}

.ant-menu-submenu-popup.side-bar-popup > .ant-menu{
  display: none;
}
